import React from 'react';
import styles from './People.module.css';
import PeopleSection from '../../PeopleSection/PeopleSection';
import { useState, useEffect } from 'react';

function PeoplePage() {
  const [members, setMembers] = useState({
    professor: [
      {
        name: 'June-Tae Kim',
        imageSrc:
          'https://cdn.builder.io/api/v1/image/assets/TEMP/220bf823ebcafac31d7eeb6c1f00c986481c2310458497b65c8c02cf8299399a?placeholderIfAbsent=true&apiKey=09e51bf2905f45f78e57fbb71de7c4b3',
        position: 'Assistant Professor, More Information',
        description: 'Artificial Intelligence R&D, Health Data Science',
        email: 'emailAddress@gmail.com',
      },
    ],
    member: [],
  });

  useEffect(() => {
    // 외부 JS 파일 로드
    const script = document.createElement('script');
    script.src = '/data/Members.js'; // public 경로의 JS 파일 로드
    script.onload = () => {
      if (window.members) {
        setMembers(window.members); // window에 저장된 데이터 사용
      }
      console.log(window.members);
    };
    document.body.appendChild(script);

    return () => {
      document.body.removeChild(script); // 메모리 누수 방지를 위해 script 제거
    };
  }, []);

  return (
    <main className={styles.people}>
      <PeopleSection type={'Professor'} memberArray={members.professor} />
      <PeopleSection type={'CurrentMember'} memberArray={members.member} />
    </main>
  );
}

export default PeoplePage;
